}
.books-search .form-control {
    position: relative;
    border: none;
    border-radius: 4px;
    background: #edf3f4;
    box-shadow: none;
    font-size: 12px;
    line-height: 20px;
}

.form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
